@import url('https://fonts.googleapis.com/css?family=Poiret+One');

.App {
  text-align: center;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
}

#drum-machine {
  text-align: center;
  margin: 0 auto;
  padding: 20px;
  background:-webkit-radial-gradient(center, ellipse, rgb(117, 117, 117) 0%, rgba(0,0,0,1) 100%);
  background:-o-radial-gradient(center, ellipse, rgb(117, 117, 117) 0%, rgba(0,0,0,1) 100%);
  background:radial-gradient(ellipse at center, rgb(117, 117, 117) 0%, rgba(0,0,0,1) 100%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 650px;
  border-radius: 25px;
}

#drum-container {
  margin: 50px;
  display: grid;
  grid-template: repeat(3, 100px) / repeat(3, 100px);
  grid-gap: 10px;
  -ms-flex-pack: center;
      justify-content: center;
  background: #7c5202;
  padding: 25px;
  border-radius: 20px;
}

.drum-pad {
  width: 100px;
  height: 100px;
  background-color: #eee;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 3px;
  -webkit-box-shadow: 0 0 2px 2px rgba(255,255,255,0.6) inset, 2px 2px 3px grey;
          box-shadow: 0 0 2px 2px rgba(255,255,255,0.6) inset, 2px 2px 3px grey;
  font-size: 30px;
}

.playing {
  -webkit-box-shadow: 0 0 2px 2px #3d3d3d inset;
          box-shadow: 0 0 2px 2px #3d3d3d inset;
  background-image: -webkit-radial-gradient(center, ellipse, rgba(100,100,100,1) 0%, white 100%);
  background-image: -o-radial-gradient(center, ellipse, rgba(100,100,100,1) 0%, white 100%);
  background-image: radial-gradient(ellipse at center, rgba(100,100,100,1) 0%, white 100%);
}

#display {
  background: #7c5202;
  width: 160px;
  padding: 15px;
  border-radius: 20px;
  -webkit-box-shadow: 0 0 2px 2px #3d3d3d inset;
          box-shadow: 0 0 2px 2px #3d3d3d inset;
  color: gainsboro;
  font-size: 20px;
}


